
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Group from 'vux/src/components/group/index.vue'

import FileItem from 'c%/FileItem.vue'

export default{
mixins: [ common, globals ],
components: {
Group,
FileItem
},
data () {
return {
info: {
eid: 0,
},
loading: false,
id: this.$route.params.id,
list: [],
publish: {}
}
},
mounted () {
this.init_data()
},
computed: {
level_img () {
let [standard_level,homework,img] = [{1:'A',2:'B',3:'C',4:'D'},this.info,'/assets/images/A.png']
let normal_level = {1:'A+',2:'A',3:'A-',4:'B+',5:'B',6:'B-',7:'C+',8:'C',9:'C-',10:'D'}
if(homework.is_check===1){
if(homework.check_level===2){
img = '/static/img/neza/org/'+normal_level[homework.result_level]+'.png'
}else{
img = '/static/img/neza/org/'+standard_level[homework.result_level]+'.png'
}
}
return img
},
},
methods: {
openNewWeb () {
if(this.publish.link_url){
window.open(this.publish.link_url)
}
},
formatDate (time) {
time = time||''
let result = time.substring(5,10)
return result
},
init_data () {
let id = this.id,
uri = util.sprintf('homework_completes/%s?with=homework_task,homework_task.homework_attachment,student,homework_reply,homework_reply.homework_attachment,homework_attachment',id)
this.$rest(uri)
.get()
.success(data=>{
this.info = data
this.init_publish()
this.loading = true
})
.error(response=>{
this.loading = true
this.toast(response.body.message||'加载错误')
})
},
init_publish () {
let hp_id = this.$route.query.hp_id

this.$rest('homework_publishs')
.add_url_param(hp_id)
.get()
.success(data=>{
this.publish = data
})
.error(response=>{
this.toast(response.body.message||'加载错误')
})
}
}
}



